import React , {Component} from 'react';
import { translate } from "react-translate";
import RedesSociales from '../Flotante';
import FooterBottom from './FooterBottom';
import FooterTop from './FooterTop';
import MailchimpSubscribe from '../Newsletter/NewsletterSubscribe'
import "./footer.css";

class Footer extends Component {

    state = {language: 'en'};
    
    render(){

           
    
        return(
            <>
                <footer className="footer_area footer_area_four f_bg">
                    <FooterTop language={this.props.language}/>
                    <MailchimpSubscribe language={this.props.language}/>
                    <FooterBottom                         
                        Intranet="Intranet"
                        privacyPolicy="privacyPolicy"
                    />
                    
                </footer>
                <RedesSociales mensajeWhatsapp="mensajeWhatsapp"/>
            </>
        )
    }
}

export default translate('footer')(Footer);