import { useState } from 'react';
//import { sanitize } from '../../utils/miscellaneous';
//import Loading from '../../utils/loading';
import { translate } from "react-translate";
import $ from 'jquery';
import "./newsletter.css";


const NewsletterForm = ( { status, message, onValidated  }) => {

  const [ error, setError ] = useState(null);
  const [ email, setEmail ] = useState(null);
  const newsletterEn = require('../../assets/translations/en.json');
  const newsletterEs = require('../../assets/translations/es.json');

  let lang = null 
  /* ------IDIOMA */

  const ActualLang = localStorage.getItem("lenguaje") ? localStorage.getItem("lenguaje") : 'en';
  if (ActualLang === 'es') {
    lang = newsletterEs
  } else {
    lang = newsletterEn
  }


  /**
   * Manejar env√≠o de formulario.
   *
   * @return {{value}|*|boolean|null}
   */
    const handleFormSubmit = () => {

      setError(null);

      if ( ! email ) {
        setError( 'Por favor, introduce una direcci√≥n de correo electr√≥nico v√°lida' );
        return null;
        
      }

      const isFormValidated = onValidated({ EMAIL: email });

      // En caso de √©xito devuelve verdadero
      return email && email.indexOf("@") > -1 && isFormValidated;
    }

  /**
   * Manejar evento de clave de entrada.
   * @param event
   */

  const handleInputKeyEvent = ( event ) => {
    setError(null);
    // Number 13 is the "Enter" key on the keyboard
    if (event.keyCode === 13) {
      // Cancel the default action, if needed
      event.preventDefault();
      // Trigger the button element with a click
      handleFormSubmit();
    }
  }




  /* Tiempo en que sale el Pop-up */
  $(document).ready(function() {
    $('.modal').delay(6000).queue(function() {
      $('#exampleModalCenter').modal('show')
    });
  });

  

  return (
    <div>
      <div class="modal newsletter fade" id="exampleModalCenter" tabindex="-2" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body px-4">
              <div className="row align-items-center">
                <div className='col-lg-6'>
                  <div className="newsletter-img" >
                    <img src={require ("../../img/process/img-devops-int.png" ).default} alt="" className='img-fluid'/>               
                  </div>
                </div>
                <div className='col-lg-6'>
                  <div>
                    <h2 className="f_size_30 l_height10 pt-4 pl-3 m-0">{lang.newsletter.newsletterH2}</h2>
                    <h1 className="f_size_50 mb-1 p_color">DreamBlog</h1>
                    <h4 className='f_size_18 f_p_black text-center p_color'>{lang.newsletter.newsletterH4}</h4>
                    <p className='f_size_10'>{lang.newsletter.newsletterP}</p>
                    <div className="position-relative text-center mx-5 flex newsletter-input-fields">
                      <div className="w-100 ">
                        <input
                          onChange={(event) => setEmail(event?.target?.value ?? '')}
                          type="email"
                          placeholder="TU DIRECCI√ìN DE CORREO"
                          className="f_700 text-center border-dark w-100 px-3 py-2 rounded-lg border bg-white "
                          onKeyUp={(event) => handleInputKeyEvent(event)}
                        />
                      </div>
                      <div className="">
                        <button className=" f_700 w-100 cursor-pointer border-dark border px-3 py-2 rounded-lg bg-warning my-2" onClick={handleFormSubmit}>
                            {lang.newsletter.newsletterBtn}
                        </button>
                      </div>
                    </div>
                  </div>

                </div>
              </div>
              <div className="col-lg-12 text-right">
                    { 'sending' === status ?  <div>{lang.newsletter.sending}</div>
                      : null }
                    {'error' === status || error ? (
                      <div className=" pt-3" >{lang.newsletter.error}</div>
                    ) : null }
                    {'success' === status && 'error' !== status && !error && (
                      <div className=" font-bold pt-2" >{lang.newsletter.success}</div>
                    )}
                  </div>
            </div>
          </div>
        </div>  
      </div> 
    </div>
  );
}

export default translate('newsletter')(NewsletterForm);
