import React, {Component} from 'react';
import { translate } from "react-translate";
import emailjs from 'emailjs-com';
import ReCAPTCHA from "react-google-recaptcha";

class InfoContacts extends Component {
 

    render(){
        let {¬†t } = this.props;
       
        function sendEmail(e) {
            e.preventDefault();
        
            emailjs.sendForm('service_gmail', 'template_dreamcode', e.target, 'user_DK3x36R2nsn4W5NwGBIou')

              .then((result) => {
                  console.log(result.text + " ENVIADO");
                  
              }, (error) => {
                  console.log(error.text + " ERROR");
                  
              });

              e.target.reset()
         
                    const modal = document.querySelector(".modal")
                    const closeBtn = document.querySelector(".close")
                    modal.style.display = "block";
                    closeBtn.addEventListener("click", () => {
                      modal.style.display = "none";
                    })
                  
        }
        function onChange(value) {
            console.log("Captcha value:", value);
          }

        return(
            <section className="contact_info_area  bg-light bg_color" id="contact">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-2"></div>
                        <div className="contact_form col-lg-8">
                            <h2 className="f_p f_size_22 p_color f_600 l_height28 mb_40">{t(this.props.title)}</h2>

                            <form onSubmit={sendEmail} className="contact_form_box needs-validation was-validated" method="post" id="contactForm" >
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="form-group">
                                            <input className="form-control" type="text" id="name" name="name" placeholder={t(this.props.name)} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group">
                                            <input className="form-control" type="email" name="email" id="inputEmail4" placeholder={t(this.props.email)} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group">
                                            <input className="form-control" type="text" id="company" name="company" placeholder={t(this.props.company)} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text-left">
                                            <label className="text-left f_600 m_color" for="exampleFormControlSelect1">{t(this.props.address)}</label>
                                            <input className="form-control" type="text" id="location" name="location" placeholder={t(this.props.location)} required/>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-group text_box text-left">
                                        <label className="text-left f_600 m_color" for="exampleFormControlSelect1">{t(this.props.titleServiceOpc)}</label>
                                        <select name="service" className="custom-select my-1 mr-sm-2" id="inlineFormCustomSelectPref">
                                                <option name="Develop" value="Develop">{t(this.props.ServOpc1)}</option>
                                                <option name="Business" value="Business">{t(this.props.ServOpc2)}</option>
                                                <option name="Staff" value="Staff">{t(this.props.ServOpc3)}</option>
                                                <option name="Information" value="Information">{t(this.props.ServOpc4)}</option>
                                                <option name="Database" value="Database">{t(this.props.ServOpc5)}</option>
                                            </select>                                          
                                        </div>
                                    </div>
                                    
                                    <div className="col-lg-12">
                                        <div className="form-group ">
                                            <textarea className="form-control"  name="message" id="message" cols="30" rows="10" placeholder={t(this.props.message)} required></textarea>
                                        </div>
                                    </div>
                                    <div className="col-lg-12">
                                        <div className="form-check text-left mb-3">
                                            <input className="form-check-input position-static" type="checkbox" id="blankCheckbox" value="option1" aria-label="..." required/>                        
                                            <label className="pl-2 p_color form-check-label" for="gridCheck">{t(this.props.txt_privacyPolicy)}
                                                <a href={require ("../../Doc/Tratamiento_de_Datos_Dreamcode.pdf").default} className='t_color' download="Tratamiento_de_Datos_Dreamcode.pdf">{t(this.props.privacyPolicy)}</a>
                                            </label>
                                            <div class="invalid-feedback">Example invalid feedback text</div>
                                        </div>
                                    </div>
                                </div>
                                <div className="mb-3 form-check ">
                                    <ReCAPTCHA
                                        sitekey="6Le64SIfAAAAAEoQdq4n9_iMt8dGeqWeYgJzs5qe"
                                        onChange={onChange}
                                        size="invisible"
                                    />
                                </div>    
                                <button type="submit" onclick='sendEmail()' className=" mb-1 btn_six slider_btn" >{t(this.props.btn)}</button>
                                
                            </form>
{
   //MODAL
}
                             <div className="modal" >
                                <div className=" modal-dialog modal-dialog-centered">
                                    <div className="modal-content">
                                    <div className="modal-header">
                                    
                                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                        <span class="close" aria-hidden="true">&times;</span>
                                        </button>
                                    </div>
                                    <div className="modal-body">
                                        <h3 className='m_color f_p text-center'>{t(this.props.textModal)}</h3>
                                    </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-2"></div>
                    </div>
                    
                </div>
            </section>
        )

        
    }
}


export default translate('contact_form')(InfoContacts);