import React from 'react';
import { useParams , Link} from "react-router-dom";
//import MetaDatosPost from '../MetaDatos/MetaDatosPost';
import postlist from "../../posts.json"
import Markdown from "react-markdown"
import Reveal from 'react-reveal/Reveal/';
import ReactGA from 'react-ga';

const Categorys = () => {

  let { topicId } = useParams();
  let Stay = null

        if (topicId==="up-to-date") {
            Stay = "Stay up-to-date"
        } else {
            Stay = topicId
        }


    // Configuraci√≥n de Google Analytics
        const gaTrackingId = "UA-194698672-1"; 
        ReactGA.initialize (gaTrackingId); 
        ReactGA.pageview ("post/" + topicId );

        //Imprime solo las primeras 20 palabras del articulo
        const excerptList = postlist.map(post => {
          return post.content.split(" ").slice(0, 20).join(" ") + "..."
      })

    return (
        <>    

              <section className={`banner_area h-100 ${topicId} sec_pad2`} id="bannerBlog">
                <div className="container">
                    <div className="">                   
                        <div className="row">
                            <div className="col-md-12 text-center seo_banner_content">
                                <Reveal effect="fadeInUp" duration={500}>
                                    <h1 className='text-capitalize'>{Stay}</h1>        
                                </Reveal>
                            </div>
                        </div>
                    </div>
                </div>
              </section>

          <section className="postlist portfolio_area row">
            <div className="container">
            <div>
                  <nav aria-label="breadcrumb">
                      <ol className="breadcrumb">
                          <li className="f_p f_size_20 breadcrumb-item"><a href="/blog">DreamBlog</a></li>
                          <li className="f_p f_size_20 breadcrumb-item "><a href="/category">Category</a></li>
                          <li className="f_p f_size_20 breadcrumb-item text-capitalize active" aria-current="page">{Stay}</li>
                      </ol>
                  </nav>
                </div>
              <div className="row portfolio_gallery mb_30" id="articulos-post">
                        { postlist.map((post, i) => {
                            if (post.category === topicId) {
                                return (                                      
                                    
                                        <div className={`portfolio_item mb-30 col-lg-6 col-sm-12 ${post.category} `}>                                                               
                                                <div className="imgprueba">
                                                    <img className="img-fluid" src={require(`../../img/posts-Img/${post.imglink}`).default} alt=""/>
                                                </div>
                                                <div className="blog_content">
                                                    <h2 className="f_p f_size_30 f_600 t_color ">
                                                        <Link className="links f_p" to={`/${post.url}`}>{post.title}</Link>
                                                    </h2>
                                                    <div className="entry_post_info mb-2 text-capitalize">
                                                        <a href={`/${post.url}`}>{post.date}</a>
                                                        <a href={`/${post.url}`}>{post.read}</a>                                                        
                                                        <a href={`/category/${post.category}`}>{post.category}</a>
                                                    </div>
                                                    <Markdown children={excerptList[i]} skipHtml={false} />
                                                    <div className="post_tag d-flex ">
                                                        <a href={`/category/${post.category}`}>
                                                            <p className='text-capitalize'>{post.category}</p>
                                                        </a>                                                   </div>                                                
                                                </div>
                                        </div>        
                                )
                            }
                            })
                        }
              </div>
            </div>
          </section>
        </>
      );
}
export default Categorys;